export default {
	data() {
		return {
			globaluser:{},
			sex:-1

		}
	},
	onLoad() {
		var me = this;
		var globaluser = uni.getStorageSync("globalUser");
		me.globaluser = globaluser;
		me.sex = me.globaluser.sex;
	},
	methods: {
		formSubmitSex(e){
			
			var me = this;
			me.globaluser.sex = me.sex;
			uni.setStorageSync("globalUser",me.globaluser);
			uni.switchTab({
				url:"../me/me"
			})
			
		},
		changeSex(e){
			//console.log(e);
			var sex = e.detail.value;
			this.sex = sex;
		}
	}
}
